.main_content_container {
    width: 100%;
    margin: 0 auto;
    margin-block-start: 10px;
    margin-top: 10px;
    margin-block-end: 10px;
    margin-bottom: 10px;
    padding-block-end: 10px;
    padding-bottom: 10px;
    position: relative;
}

.main_content_container:lang(ar) {
    padding-right: 45px;
    padding-left: 10px;
}

.main_content_container:lang(en) {
    padding-left: 45px;
    padding-right: 10px;
}
